.beforeParent {
    display: flex;
    width: 95%;
    height: 10vh;
    background-color: white;
    justify-content: space-around;
    align-items: center;
    border: 1px solid #706897;
    border-radius: 5px;
    margin-bottom: 15px;
    padding: 7px;
}

.FirstChild {
    width: 95%;
    font-size: 20px;
    color: #14274e;
    font-weight: 600;
    background-color: white;
    margin-bottom: 2vh;
    padding: 8px;
    border: 2px solid #706897;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
}

.btn {
    background-color: #ffe5b9;
    width: 9vh;
    height: 3vh;
    color: white;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    border: none;
}

.firstDiv {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 93vh;
    width: 100%;
    background-color: #fff3e2;
}

.secondDiv {
    width: 100%;
    height: 18vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.clientSummaryContainer {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding-bottom: 3vh;
    padding-right: 5%;
    font-size: 20px;
    font-weight: 500;
    color: #14274e;
    height: 20%;
}

.listContainer {
    overflow-y: auto;
    width: 100%;
    background-color: #fff3e2;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 50vh;
    overflow-y: auto;
}

.dataContainer {
    width: 100%;
    height: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.childListContainer {
    width: 100%;
    height: 500vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.btnDiv {
    width: 100px;
    height: 40px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #ffad5a;
}

.onProgress {
    color: white;
    font-size: 22px;
    font-weight: 600;
}
