.dataContainer {
    width: 90%;
    height: 15vh;
    display: flex;
    flex-direction: column;
    margin-bottom: 2vh;
    align-items: center;
    justify-content: center;
    font-size: 22px;
    background-color: white;
    border: 2px solid #706897;
    border-radius: 5px;
}

.firstDiv {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 93vh;
    width: 100%;
    background-color: #fff3e2;
}

.logoFirstDiv {
    width: 100%;
    height: 18vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.logoSecondDiv {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.containerDiv {
    width: 100%;
    height: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
}