.nav {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-around;
    height: 7vh;
    align-items: center;
    overflow: scroll;
    background-color: white;
    box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2), 
                0px 8px 10px 1px rgba(0, 0, 0, 0.14), 
                0px 3px 14px 2px rgba(0, 0, 0, 0.12);
}

.NavSpan {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.NavIcon {
    font-size: 25px;
    color: rgba(20,39,78,0.7);
}
.NavLink {
    font-size: 15px;
    text-decoration: none;
    color: rgba(20,39,78,0.6);
}

