body {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 93vh;
    width: 100%;
    background-color: #fff3e2;
}

.secondDiv {
    width: 100%;
    height: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.sigContainer {
    width: 100%;
    height: 60%;
    margin: 0 auto;
    background-color: #fff;
    border: 2px solid #bbbfca;
    border-radius: 8px;
}

.sigPad {
    width: 100%;
    height: 100%;
    border-radius: 10px;
}

.buttons {
    width: 30%;
    height: 5vh;
    border: none;
    border-radius: 8px;
    background-color: #ffad5a;
    font-size: 24px;
    color: white;
}
.logoFirstDiv {
    width: 100%;
    height: 18vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.logoSecondDiv {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.buttonContainer {
    width: 100%;
    margin-top: 5vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
