.body {
    width: 100%;
    height: 90vh;
    background-color: #f0ece2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}
.imgDiv {
    width: 100%;
    padding-top: 2vh;
    margin-bottom: 3vh;
    margin-top: 2vh;
    display: flex;
    justify-content: center;
    margin-right: 2px;
}
.logoText {
    font-size: 28px;
    color: #14274e;
    font-weight: 700;
}
.firstSpan {
    width: 100%;
    top: 10vh;
    display: flex;
    align-items: center;
}
.firstDiv {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.onlyChild {
    font-size: 18px;
    letter-spacing: 1vh;
    font-weight: 500;
}
.beforeParent {
    display: flex;
    width: 88%;
    height: 5vh;
    background-color: white;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #14274e;
    border-radius: 2px;
    padding-right: 2vh;
    padding-left: 3vh;
    margin-bottom: 1.5vh;
}

.firstChild {
    font-size: 18px;
    color: #14274e;
    font-weight: 600;
}

.secondChild {
    font-size: 18px;
    font-weight: 500;
}

.btn {
    background-color: #ffad5a;
    width: 30%;
    height: 5vh;
    color: white;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    border: none;
    position: fixed;
    bottom: 12vh;
}

.btnDiv {
    width: 100%;
    display: flex;
    justify-content: center;
}

.secondDiv {
    width: 100%;
    display: none;
    flex-direction: column;
    align-items: center;
}