.firstDiv {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 93vh;
    width: 100%;
    background-color: #fff3e2;
}

.containerDiv {
    width: 100%;
    height: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: auto;
}

.clientContainer {
    width: 100%;
    height: 4vh;
    text-decoration: none;
    margin-bottom: 15px;
    padding-bottom: 7px;
    padding-top: 7px;
    background-color: white;
    border: 2px solid #706897;
    border-radius: 5px;
}

.clientSubContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 5%;
    padding-left: 5%;
}

.clientDetailLink {
    text-decoration: none;
    font-size: 22px;
    letter-spacing: 1px;
    color: #494949;
    font-weight: 600;
}

.clientProgressLink {
    text-decoration: none;
    background-color: #ffad5a;
    display: flex;
    align-items: center;
    color: white;
    padding: 5px;
    padding-left: 13px;
    padding-right: 13px;
    border-radius: 5px;
    font-size: 20px;
    width: 19%;
    justify-content: center;
}
.logoFirstDiv {
    width: 100%;
    height: 18vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.logoSecondDiv {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.childContainer {
    width: 100%;
    height: 3000vh;
}
