.firstDiv {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 93vh;
    width: 100%;
    background-color: #fff3e2;
}

.logoFirstDiv {
    width: 100%;
    height: 18vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.logoSecondDiv {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.textDiv {
    width: 100%;
    height: 60%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.textArea {
    border: 2px soild #bbbfca;
    border-radius: 8px;
    font-size: 20px;
}

.btnDiv {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.btn {
    width: 100px;
    height: 40px;
    border: none;
    border-radius: 8px;
    background-color: #ffad5a;
    font-size: 24px;
    color: white;
}
