.firstdiv {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 93vh;
    width: 100%;
    background-color: #fff3e2;
}
.secondDiv {
    width: 100%;
    height: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.logoFirstDiv {
    width: 100%;
    height: 18vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.logoSecondDiv {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.confirmDataSpan {
    display: flex;
    width: 88%;
    height: 5vh;
    background-color: white;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #14274e;
    border-radius: 5px;
    padding-right: 2vh;
    padding-left: 3vh;
    margin-bottom: 1vh;
}
.firstConfirmData {
    font-size: 18px;
    color: #14274e;
    font-weight: 600;
}

.secondConfirmData {
    font-size: 18px;
    font-weight: 500;
}

.firstIdDiv {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 1vh;
    margin-bottom: 3vh;
    flex-direction: column;
    height: 50vh;
}

.textArea {
    border: 2px solid #bbbfca;
    border-radius: 8px;
    font-size: 20px;
}

.firstIdBtnDiv {
    width: 100%;
    display: flex;
    justify-content: center;
}

.btn {
    background-color: #ffad5a;
    width: 20%;
    height: 4vh;
    color: white;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    border: none;
    position: fixed;
    bottom: 13vh;
}

.secondIdDiv {
    height: 55%;
    display: none;
    margin-top: 1vh;
    margin-bottom: 3vh;
    flex-direction: column;
    align-items: center;
}
.imgStyle {
    width: 95%;
    height: 100%;
    max-width: 500px;
    background-color: white;
    border: 2px solid #bbbfca;
    max-height: 900px;
    border-radius: 8px;
}

.secondIdBtnDiv {
    width: 100%;
    display: flex;
    justify-content: space-around;
}

.confirmBtnContainer {
    width: 100%;
    display: flex;
    justify-content: center;
}

.confirmBtn {
    width: 30%;
    height: 5vh;
    border: none;
    border-radius: 8px;
    background-color: #ffad5a;
    font-size: 24px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    bottom: 13vh;
}
